import Vue from "vue";
import Component from "vue-class-component";

//  Vue 
@Component
export default class TagHelper extends Vue {
  createTag(tag: Tag) {
    if (tag.name === "") {
      alert("");
    } else if (tag.icon === "") {
      alert("");
    } else {
      this.$store.commit("createTag", tag);
    }
  }
}
